.lmj-shopping-list {
	width: 100%;
}

.lmj-plant-list {
	list-style-type: none;
	padding: 32px;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
}

.lmj-plant-list>div {
	margin: 15px;
	padding: 10px;
	border-radius: 10px;
	box-shadow: 4px 4px 10px 3px gainsboro;
}

.btn {
	color: #fff;
	background-color: #31b572;
	padding: 10px 15px;
	border-radius: 15px;
	border: none;
}


@media all and (max-width: 925px) {
	.lmj-plant-list {
		padding: 10px;
	}

	.lmj-plant-list>div {
		margin: 10px 5px;
	}
}

@media all and (max-width: 720px) {
	.lmj-plant-list {
		justify-content: center;

	}

	.lmj-plant-list>div {
		margin: 15px;
	}

}

@media all and (max-width: 340px) {
	.lmj-plant-list {
		padding: 10px 5px;
	}
	.lmj-plant-list>div {
		margin: 10px 2px;
	}
}